.wrapper{
    display: flex;
    justify-content:space-between;
    flex-wrap: wrap;
    align-items:center;
    padding-top: 50px;

}

.text {
    border-bottom: 6px solid #dddddd;
    border-radius: 5px;
    color: #919191;
    padding-bottom: 15px;
}

.text h1 {
    color: #919191;
    letter-spacing: .04em
}
.box_progress{
    display: flex;
    flex-wrap: wrap;
    justify-content:space-between;
    padding-bottom: 50px;

}
@media screen and (max-width: 575px) {
    .wrapper {
      justify-content:center;
        padding-top: 10px;
    }
}
