.skill {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding:20px
}

.name {

    text-align: center;
    padding: 55px 0;
    font-size: 36px;
    font-weight: 900;
}

.text {

    font-size: 24px;
    max-width: 230px;
    padding-bottom: 55px;
}

.avatar {
    display: flex;
    justify-content: center
}

.avatarI {
    height: 100px;
    width: 100px;
}
.MuiLinearProgress-barColorPrimary {
    background-color: #00f8ff;
}
@media screen and (max-width: 575px) {
    .skill {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding:20px;
        margin-top: 50px;
        max-width: 250px;
    }

    .name {

        text-align: center;
        padding: 35px 0;
        font-size: 28px;
        font-weight: 900;
    }

    .text {

        font-size: 20px;
        max-width: 230px;
        padding-bottom: 40px;
    }

    .avatar {
        display: flex;
        justify-content: center
    }

    .avatarI {
        height: 70px;
        width: 70px;
    }
}

